.testimonial-client {
    display: flex;
    align-items: center;
}

.client-info {
    flex-grow: 1;
    margin-left: 20px;
    padding-top: 20px;
}

.client-name {
    color: #222;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.388;
    margin-bottom: 6px;
}

.client-role {
    color: #333;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.58;
}

.client-img {
    border-radius: 50%;
    height: 60px;
    width: 60px;
    flex-shrink: 0;
}

.testimonial-card {
    border: 1px solid lightgrey;
    background: #fff;
    padding: 44px 40px;
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
}